import setting from '../settings/index.js';
const { BASE_URL } = setting;

import { renderCountries, errorMsg } from '../renderCountries';
import refs from '../DOMRefs'

 function countriesAPi() {
    if (!refs.searchInput.value) {
        refs.countriesContainer.innerHTML = '';
        return;
    }
    return fetch(`${BASE_URL}/name/${refs.searchInput.value}`)
        .then(responce => {
            if (!responce.ok) throw new Error;
            return responce.json()
        }
        )
        .then(renderCountries)
        .catch(errorMsg);
}

export default countriesAPi